<template>
  <div class="mediumButton">
    <button class="enable" v-if="enable" @touchstart="touchDown()" @touchend="touchEnd" @click="callBack">
      <span class="text">预约
        <span class="leftNum">({{num}})</span>
      </span>
    </button>
    <button class="unable" v-else>
      <span class="text">
        约满
      </span>
    </button>
    <div class="mask" ref="mask"></div>
  </div>

</template>

<script>
export default {
  name: 'mediumButton',
  props: ['enable', 'num', 'callBack'],
  methods: {
    touchDown () {
      this.$refs.mask.style.display = 'block'
    },
    touchEnd () {
      this.$refs.mask.style.display = 'none'
    }
  }
}
</script>

<style scoped lang="less">
  .mediumButton {
    position: relative;

    button {
      padding: 0;
      border: 0;
      border-radius: 0.02*2rem;
      width: 1.8rem;
      height: .6rem;
      background: #3391e8;
      box-shadow: 0 0.04*2rem 0.06*2rem 0 rgba(45, 136, 219, 0.30);
      color: #fff;
      font-size: .13*2rem;
      font-family: PingFangSC-Regular, sans-serif;
      text-align: center;
      align-items: center;
      .text{
        letter-spacing: 0.08rem;
        line-height: normal;
        padding: 0.06*2rem .15*2rem 0.07*2rem 0.16*2rem;
        .leftNum{
          letter-spacing: 0;
        }
      }
    }

    .unable {
      background: #F1F1F1;
      box-shadow: none;
      color: #B1AFAF;
    }

    .mask {
      display: none;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      width: .9*2rem;
      height: .3*2rem;
      background-color: #000;
      opacity: 0.3;
      border-radius: 0.02*2rem;
    }
  }
</style>



