<template>
  <div class="evaluation">
    <div class="evaluationItem" v-for="(item,index) in doctorCommentList" :key="index">
      <div class="nameBox">
        <span class="name">@{{item.userName}}</span>
        <span class="like" @click="likeFn(index,item.id,item.likeNum)" ref="like">
          <i></i>
          <span class="num">{{item.likeNum}}</span>
        </span>
      </div>
      <div class="contentBox">{{item.comments}}</div>
      <div class="tagBox">
        <span class="tag" v-for="(item,index) in item.targsList">
          <p class="text">{{item}}</p>
        </span>
      </div>
    </div>
    <div
      class="loading"
      v-show="doctorCommentList.length>5&&curCommentPage<=curCommentTotalPage"
    >加载中...</div>
    <div class="end" v-show="curCommentPage>curCommentTotalPage">没有更多评论啦..</div>
  </div>
</template>

<script>
import { apiUrl } from "@/common/js/api.js";
import { ajaxPost } from "@/common/js/public.js";
import store from "@/vuex/index";
export default {
  name: "evaluation",
  data() {
    return {};
  },
  computed: {
    doctorCommentList() {
      return store.state.wmodule.curDoctorComment.list;
    },
    curCommentPage() {
      return store.state.wmodule.curDoctorComment.page;
    },
    curCommentTotalPage() {
      return store.state.wmodule.curDoctorComment.totalPage;
    }
  },
  created() {
    // store.commit("wmodule/changeDoctorCommentList", {
    //   data: {
    //     commentList: []
    //   }
    // });

    const { doctorId } = this.$route.params;
    const { limit, page } = store.state.wmodule.curDoctorComment;
    this.$store.dispatch("wmodule/getDoctorCommentByDoctorId", {
      doctorId: doctorId,
      limit: limit,
      page: page
    });
  },
  methods: {
    // 评论id 是否点赞 1已点赞 0未点赞
    likeFn(index, commentId, hasLike) {
      // likeOrNo 请求所需的参数 1点赞 0取消点赞
      // const likeOrNo = hasLike === 0 ? "1" : "0";
      if (this.$refs.like[index].className.indexOf("active") > 0) {
        console.log(this.$refs.like[index].className)
        this.$refs.like[index].className = 'like'
        console.log(this.$refs.like[index].className)
      } else {
        ajaxPost(
          apiUrl.userLikeDoctComment,
          {
            commentId: commentId,
            likeOrNo: "1"
          },
          res => {
            const resData = res.data.body; // 点赞请求
            if (resData.resultCode === "1000") {
              //this.$refs.like.className
              this.doctorCommentList[index].likeNum += 1;
              this.$nextTick(() => {
                this.$refs.like.forEach((likeItem, likeIndex) => {
                  if (likeIndex === index) {
                    likeItem.className += " active";
                  }
                });
                this.doctorCommentList[index].likeNum = this.doctorCommentList[index].likeNum;
              });
            }
          }
        );
      }
    }
  }
};
</script>

<style scoped lang="less">
.evaluation {
  width: 100%;
  .evaluationItem {
    width: 100%;
    box-sizing: border-box;
    padding: 0.28rem 0.3rem 0.4rem;
    border-bottom: 0.02rem solid #f6f7f8;
    .nameBox {
      .name {
        font-family: PingFangSC-Medium, sans-serif;
        font-size: 0.28rem;
        color: #272727;
      }
      .like {
        float: right;
        vertical-align: middle;
        height: 0.4rem;
        box-sizing: border-box;
        i {
          display: inline-block;
          background: url("../../../assets/appointment/docdetail_com_icon_aree_pre@2x.png")
            center no-repeat;
          width: 0.26rem;
          height: 0.28rem;
          background-size: 100% 100%;
        }
        .num {
          position: relative;
          bottom: 0.02rem;
          margin-left: 0.1rem;
          font-family: PingFangSC-Medium, sans-serif;
          font-size: 0.24rem;
          color: #b1afaf;
        }
        &.active {
          i {
            background: url("../../../assets/appointment/docdetail_com_icon_aree@2x.png")
              center no-repeat;
            background-size: 100% 100%;
          }
          .num {
            color: #ff941f;
          }
        }
      }
    }
    .contentBox {
      width: 100%;
      padding: 0.24rem 0 0.22rem;
      word-wrap:break-word; // 解决英文和数字不会自动换行的问题
      white-space: normal;
      font-family: PingFangSC-Regular, sans-serif;
      font-size: 0.24rem;
      color: #272727;
    }
    .tagBox {
      .tag {
        margin-top: .2rem;
        display: inline-block;
        box-sizing: border-box;
        height: 0.4rem;
        border: 0.02rem solid #3391e8;
        border-radius: 0.04rem;
        padding: 0.04rem 0.2rem;
        margin-right: 0.12rem;
        .text {
          line-height: normal;
          font-family: PingFangSC-Light, sans-serif;
          font-size: 0.22rem;
          color: #3391e8;
        }
      }
    }
  }
  .loading,
  .end {
    box-sizing: border-box;
    padding: 0.3rem 0;
    width: 100%;
    height: 0.4rem;
    text-align: center;
    font-size: 0.18rem;
    font-family: PingFangSC-Medium, sans-serif;
    color: #b1afaf;
  }
}
</style>



