<template>
  <div class="doctorDetails">
    <Doctor/>
    <Tabs @changeTab="changeTab" :tabNum="tabNum"/>
    <!-- 分隔条 -->
    <div class="bgLine"></div>
    <div class="contentBox" ref="contentBox">
      <!-- 下拉刷新 -->
      <mescroll-vue ref="mescroll" :down="mescrollDown" @init="mescrollInit" v-if="tabNum===1">
        <div class="appointment">
          <AppointTable
            v-for="item in curDoctorWork.list"
            :doctorWorkList="item"
            :details="curDoctorWork"
            :key="item.id"
          />
        </div>
      </mescroll-vue>
      <!-- 上拉加载 -->
      <div class="evaluation" ref="evaluationBox" v-else>
        <Evaluation/>
      </div>
    </div>
  </div>
</template>

<script>
import Doctor from "./doctor"; // 头部医生信息
import Tabs from "./tabs";
import evaluationVue from "./evaluation.vue";
import AppointTable from "./appointTable"; // 预约表单
import Evaluation from "./evaluation"; // 评论组件
import store from "@/vuex/index";
//import MescrollVue from "mescroll.js/mescroll"; // 引入mescroll组件，实现下拉刷新
import { apiUrl } from "@/common/js/api.js";
import { ajaxPost } from "@/common/js/public.js";
import { clearTimeout, setTimeout } from "timers";
export default {
  name: "doctorDetails",
  data() {
    return {
      doctorId: this.$route.params.doctorId,
      tabNum: 1, // 当前选项卡的编号 1排班 2评论
      mescroll: null, // mescroll实例对象
      mescrollDown: {
        // 下拉刷新的配置
        callback: this.downCallback, // 下拉刷新的回调
        autoShowLoading: true,
        outOffsetRate: 0, // 值越接近于0，则表示下拉的距离大于offset时，越难下拉
        offset: 60,
        inOffsetRate: 1, // 值越接近于0越难下拉
        isBounce: false, // 禁止ios回弹
        afterLoading: function(mescroll) {
          return 400;
        },
        htmlContent: '<p class="downwarp-tip"></p>',
        textInOffset: `<i class="down" style="width:.14rem ;height:.16rem;margin-right: 0.08rem"></i><img style="width:.14rem ;height:.16rem;"  src="data:image/png;base64," alt="">下拉刷新`, // 文本内容
        textLoading: `<i class="loading" style="width:.14rem ;height:.16rem;margin-right: 0.08rem"><img style="width:.14rem ;height:.16rem;" src="data:image/png;base64," alt=""></i>加载中`,
        textOutOffset: `<i class="success" style="width:.14rem ;height:.16rem;margin-right: 0.08rem"><img style="width:.14rem ;height:.16rem;" src="data:image/png;base64," alt=""></i>释放刷新`,
        dataList: [] // 列表数据
      },
      noMescrollDown: {
        use: false,
        isLock: true
      },
    };
  },

  components: {
    Doctor,
    Tabs,
    AppointTable,
    Evaluation,
    //MescrollVue
  },
  methods: {
    // 修改当前页面选项卡显示的方法 1->显示医生排班 2->显示医生评价
    changeTab(tabNum) {
      this.tabNum = tabNum;
    },
    mescrollInit(mescroll) {
      this.mescroll = mescroll;
    },
    // 下拉回调
    async downCallback(mescroll) {
      await ajaxPost(
        apiUrl.getDoctorWork,
        {
          doctorId: this.doctorId
        },
        res => {
          const resData = res.data.body.data; // 医生列表
          console.log(resData);
          store.commit("wmodule/changeDoctorWork", resData);
          this.$nextTick(() => {
            mescroll.endSuccess();
          });
        }
      );
    },
    // 节流函数
    // 防止300ms内多次重复请求
    throttle(method, context) {
      clearTimeout(method.tId);
      method.tId = setTimeout(function() {
        method.call(context);
      }, 300);
    }
  },
  computed: {
    // 工作排班表
    curDoctorWork() {
      return store.state.wmodule.curDoctorWork;
    },
    // 当前页数
    curCommentPage() {
      return store.state.wmodule.curDoctorComment.page;
    },
    // 总页数
    curCommentTotalPage() {
      return store.state.wmodule.curDoctorComment.totalPage;
    },
    // 是否在加载中
    commentIsLoading() {
      return store.state.wmodule.curDoctorComment.isLoading;
    }
  },
  watch:{
    // 监听如果是从预约管理页面中跳转过来的，那就是查看医生更多评价的操作，所以执行changeTab(2)将选项卡转到评价页面
    $route(now,old){
      if(old.path.indexOf('/user/appointmentManagement')){
        this.changeTab(2)
      }
    }
  },
  created() {
    store.commit("wmodule/resetPage"); //每进入到一个医生的详情页，就将评论列表的初始page重置为1
    const {
      doctorId,
      doctorName,
      doctorLevel,
      curDoctorJobTitle
    } = this.$route.params;
    store.commit("wmodule/changeDoctor", {
      doctorId: doctorId,
      doctorName: doctorName,
      doctorLevel: doctorLevel
    });

    console.log(this.$router);
  },
  updated() {
    const { doctorId } = this.$route.params;
    const contentBox = this.$refs.contentBox;
    const evaluationBox = this.$refs.evaluationBox;
    const self = this; // 缓存Vue组件实例
    if (this.tabNum === 2) {
      contentBox.onscroll = function() {
        if (
          self.commentIsLoading === false &&
          this.scrollTop + this.clientHeight + 10 >=
            evaluationBox.clientHeight &&
          self.curCommentPage <= self.curCommentTotalPage
        ) {
          self.isLoading = true;
          this.throttle(
            store.dispatch("wmodule/getDoctorCommentByDoctorId", {
              doctorId: doctorId,
              limit: "5",
              page: self.curCommentPage
            })
          );
        }
      };
    }
  },
  mounted() {
    console.log("mounted");
    // 页面根字体大小
    const rootFontSize = parseFloat(document.documentElement.style.fontSize);
    // 根据页面字体大小，自动计算contentBox的高度
    this.$refs.contentBox.style.height =
      document.documentElement.clientHeight / rootFontSize -
      3.98 -
      1.08 -
      0.5 +
      "rem";
    // this.mescroll = new Mescroll("mescroll");
  }
};
</script>

<style scoped lang="less">
.appointment {
  background: #fff;
  padding: .4rem 0.15 * 2rem 0.49 * 2rem 0.15 * 2rem;
}

.bgLine {
  width: 100%;
  height: 0.2rem;
  background: #f6f7f8;
}

.contentBox {
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    display: none !important;
  }
  .mescroll {
    overflow-y: scroll;
    overflow-x: hidden;

    &::-webkit-scrollbar {
      display: none !important;
    }
  }
}

.evaluation {
  padding-bottom: 0.49rem;
  width: 100%;
  overflow-y: scroll;
  overflow-x: hidden;

  &::-webkit-scrollbar {
    display: none !important;
  }
}

</style>



