<template>
  <div class="tabs">
    <div class="tabBox">
      <span class="schedule" :class="{active:tabNum===1}" @click="tabFn(1)">排班</span>
      <span class="evaluation" :class="{active:tabNum===2}" @click="tabFn(2)">评价</span>
    </div>
    <div class="bar" ref="bar" :style="{left:tabNum===1?'1.6rem':'5.36rem'}" ></div>
  </div>
</template>

<script>
export default {
  props: {
    tabNum: Number
  },
  methods: {
    tabFn (tabNum) {
      this.$emit('changeTab', tabNum)
    }
  },
  mounted () {

  }
}
</script>

<style scoped lang="less">
  .tabs {
    width: 100%;
    box-sizing: border-box;
    height: 0.88rem;
    padding: .18rem 0 0.2rem 0;

    .tabBox {
      width: 100%;
      display: flex;

      span {
        flex: 1;
        font-family: PingFangSC-Medium, sans-serif;
        font-size: .32rem;
        text-align: center;
        color: #272727;

        &.active {
          color: #3391E8;
        }
      }
    }

    .bar {
      position: relative;
      margin-top: 0.02rem;
      left: 1.6rem; //5.36
      height: 0.04rem;
      width: .56rem;
      background: #3391E8;
      border-radius: 0.04rem;
      transition: all .3s ease;
    }
  }

</style>



