<template>
  <div class="doctor">
    <!-- 头像 -->
    <div class="avatar">
      <img :src="doctorWork.logoUrl" alt v-if="doctorWork.logoUrl">
      <img src="../../../assets/appointment/docList_avatar.png" alt v-else>
    </div>
    <!-- 医生介绍 -->
    <div class="introduce">
      <div class="name">
        <span class="docName">{{doctorWork.doctName}}</span>
        <span class="tag">
          <span v-if="doctorWork.isExpert==1">专家号</span>
          <span v-else>普通号</span>
        </span>
        <span class="level">{{doctorWork.jobTitle}}</span>
        <!-- 医生评分 -->
        <ShowRateStar :docRate="docRate" :color="rateNumColor" :fontFamily="rateNumFont"/>
      </div>
      <!-- 医院名称 -->
      <div class="hospital">
        <span>{{doctorWork.hospitalName}}</span>
      </div>

      <!-- 关于科室 -->
      <span class="aboutDep">
        <span class="text">关于{{doctorWork.deptName}}</span>
        <span class="arrow">
          <img src="../../../assets/appointment/right_arrow.png" alt>
        </span>
      </span>
    </div>
  </div>
</template>

<script>
import store from '@/vuex/index'
import ShowRateStar from '@/components/showRateStar'
export default {
  name: 'doctor',
  components: {
    ShowRateStar
  },
  data () {
    return {
      //在医生详情界面评分数字的显示字体与颜色与医生列表页面的数字不同
      rateNumColor: '#fff', // 评分数字颜色
      rateNumFont: 'PingFangSC-Medium,sans-serif' // 评分数字字体
    }
  },
  computed: {
    doctorWork () {
      return store.state.wmodule.curDoctorWork
    },
    docRate(){
      return store.state.wmodule.curDoctorWork.level
    }
  },
  created () {
    const { doctorId } = this.$route.params
    store.dispatch('wmodule/getDoctorWork', doctorId)
  },
  mounted () {
    console.log(this.docRate);
  }
}
</script>

<style scoped lang="less">
.doctor {
  margin-top: -0.44 * 2rem;
  width: 100%;
  height: 3.98rem;
  background: url("../../../assets/appointment/docdetail_bg.png") center
    no-repeat;
  background-size: 100% 3.98rem;
  display: flex;

  .avatar {
    width: 0.58 * 2rem;
    height: 0.66 * 2rem;
    overflow: hidden;
    border-radius: 0.04 * 2rem;
    margin: 0.44 * 2rem 0.3 * 2rem 0 0.15 * 2rem;

    img {
      display: block;
      width: 100%;
      height: 100%;
    }
  }

  .introduce {
    // flex: 1;
    padding-top: 0.44 * 2rem;
    position: relative;
    box-sizing: border-box;
    width: auto;

    .name {
      position: relative;

      .docName {
        font-family: PingFangSC-Regular, sans-serif;
        font-size: 0.15 * 2rem;
        color: #fff;
        line-height: 0.21 * 2rem;
      }

      .tag {
        margin-left: 0.06 * 2rem;
        margin-top: 0.02 * 2rem;
        top: 0;
        position: absolute;
        background-color: rgba(241, 241, 251, 0.3);
        border-radius: 0.02 * 2rem;
        box-sizing: border-box;
        width: 0.5 * 2rem;
        height: 0.16 * 2rem;
        align-items: center;
        font-weight: normal;
        z-index: 1;
        font-family: PingFangSC-Regular, sans-serif;
        font-size: 0.1 * 2rem;
        color: #ffffff;
        vertical-align: middle;
        padding: 0.02rem 0.1rem;
        span {
          line-height: normal;
          font-size: 0.1 * 2rem;
          -webkit-text-size-adjust: 100%;
          opacity: 0.8;
        }
      }

      .level {
        margin-top: 0.04 * 2rem;
        display: block;
        font-family: PingFangSC-Regular, sans-serif;
        font-size: 0.09 * 2rem;
        line-height: 0.13 * 2rem;
        opacity: 0.8;
        color: #fff;
      }
      .showRateStar {
        margin-top: 0.2rem;
      }
    }

    .hospital {
      position: absolute;
      /*right: .9*2rem;*/
      // top: 1.08 * 2rem;
      bottom: 0.96rem;
      width: auto;
      display: inline-block;
      box-sizing: border-box;
      background: #2d78bd;
      border-radius: 0.02 * 2rem;
      height: 0.2 * 2rem;
      padding: 0.06rem 0.06 * 2rem;
      align-items: center;
      font-family: PingFangSC-Regular, sans-serif;
      color: #fff;
      font-size: 0.1 * 2rem;

      span {
        height: 0.14 * 2rem;
        width: 100%;
        white-space: nowrap;
      }
    }

    .aboutDep {
      position: absolute;
      bottom: 0.36rem;
      height: 0.2 * 2rem;
      line-height: 0.14 * 2rem;
      font-family: PingFangSC-Regular, sans-serif;
      font-size: 0.1 * 2rem;
      color: #fff;
      padding: 0.03 * 2rem 0.06 * 2rem 0.03 * 2rem 0.06 * 2rem;
      box-sizing: border-box;
      background: #2d78bd;
      border-radius: 0.02 * 2rem;
      overflow: hidden;
      .arrow {
        position: relative;
        top:0.02rem;
        height: 0.2rem;
        line-height: 0.2rem;
        img {
          display: inline-block;
          width: 0.2rem;
          height: 0.2rem;
        }
      }
    }
  }
}
</style>



