<template>
  <div class="appointTable">
    <table class="table" border="1">
      <tr>
        <th rowspan="2" class="time">
          <div class="text">
            <span class="week">{{doctorWorkList.workDay}}</span>
            <span class="date">{{doctorWorkList.workTime | formatTime}}</span>
          </div>
        </th>
        <td class="today">上午</td>
        <td class="button">
          <MediumButton
            :enable="doctorWorkList.amifhas"
            :num="doctorWorkList.amleftnum"
            :callBack="()=>{jumpFn(doctorWorkList.signFree,doctorWorkList.workTime,doctorWorkList.workDay,doctorWorkList.amDoctScheduleId,doctorWork.id,doctorWork.doctName, doctorWork.isExpert,doctorWork.deptId,doctorWork.deptName,doctorWork.hospitalId,doctorWork.hospitalName)}"
          />
        </td>
      </tr>
      <tr>
        <td class="today">下午</td>
        <td class="button">
          <MediumButton
            :enable="doctorWorkList.pmifhas"
            :num="doctorWorkList.pmleftnum"
            :callBack="()=>{jumpFn(doctorWorkList.signFree,doctorWorkList.workTime,doctorWorkList.workDay,doctorWorkList.pmDoctScheduleId,doctorWork.id,doctorWork.doctName, doctorWork.isExpert, doctorWork.deptId,doctorWork.deptName,doctorWork.hospitalId,doctorWork.hospitalName)}"
          />
        </td>
      </tr>
    </table>
  </div>
</template>

<script>
import MediumButton from "@/components/mediumButton";
import store from "@/vuex/index";
export default {
  name: "appointTable",
  data() {
    return {
      enable: true,
      num: 30
    };
  },
  props: ["doctorWorkList"],
  filters: {
    formatTime(time) {
      return time.substr(5);
    }
  },
  computed: {
    doctorWork() {
      return store.state.wmodule.curDoctorWork;
    }
  },
  components: {
    MediumButton
  },
  methods: {
    // appointDate 预约日期
    // appointWorkDay 周几
    // aorp 上午1 下午2

    jumpFn(
      fee,
      appointDate,
      appointWorkDay,
      doctScheduleId,
      doctId,
      doctName,
      doctLevel,
      deptId,
      deptName,
      hospitalId,
      hospitalName
    ) {
      // console.log(doctScheduleId, "doctScheduleId");
      store.commit("wmodule/changeFee", fee);
      store.commit("wmodule/changeDoctor", {
        doctorId: doctId,
        doctorName: doctName,
        doctorLevel: doctLevel
      });
      store.commit("wmodule/changeDept", {
        deptId: deptId,
        deptName: deptName
      });
      store.commit("wmodule/changeHospital", {
        hospitalId: hospitalId,
        hospitalName: hospitalName
      });
      store.commit("wmodule/changeAppointmentDate", {
        doctScheduleId: doctScheduleId
      });

      this.$router.push({
        name: "selectNum",
        params: {
          doctScheduleId: doctScheduleId,
          appointDate: appointDate,
          appointWorkDay: appointWorkDay
        }
      });
    }
  }
};
</script>

<style scoped lang="less">
.appointTable {
  width: 100%;

  .table {
    width: 100%;
    border: 1px solid #f1f1f1;
    border-top: 0;
    border-collapse: collapse;
    text-align: center;

    tr {
      border: 1px solid #f1f1f1;
    }

    td {
      border: 1px solid #f1f1f1;
      vertical-align: middle;
    }

    .time {
      width: 0.88 * 2rem;
      height: 0.88 * 2rem;
      font-size: 0.14 * 2rem;
      font-family: PingFangSC-Regular, sans-serif;
      font-weight: normal;
      position: relative;

      .text {
        position: absolute;
        left: 50%;
        top: 50%;
        transform: translate(-50%, -50%);

        span {
          display: block;
        }
        .date {
          margin-top: 0.04 * 2rem;
          opacity: 0.8;
        }
      }
    }

    .today {
      width: 0.8 * 2rem;
      height: 0.44 * 2rem;
    }

    .button {
      width: 1.77 * 2rem;
      height: 0.44 * 2rem;
      position: relative;

      .mediumButton {
        position: absolute;
        left: 50%;
        top: 0.07 * 2rem;
        bottom: 0.07 * 2rem;
        transform: translateX(-50%);
      }
    }
  }
}
</style>



