<template>
  <div class="showRateStar clearfix">
    <div class="rateStar">
      <div
        v-for="(index,item) in 5"
        :key="index"
        class="star"
        :class="[{'full':index<=docRate}]"
        ref="star"
      ></div>
    </div>
    <span
      class="score"
      :style="{color:color!=''?color:'#272727','font-family':fontFamily!=''?fontFamily:'PingFangSC-Light, sans-serif'}"
    >{{docRate | docRateNum}}</span>
  </div>
</template>

<script>
import store from "@/vuex/index";
export default {
  props: ["docRate", "color", "fontFamily"],
  props:{
      docRate:{
        type:Number,
        default:3.0
      },
      color:{
        type:String
      },
      fontFamily:{
        type:String
      }
  },
  data() {
    return {};
  },
  filters:{
    docRateNum(num){
      return num.toFixed(1)
    }
  },
  mounted() {
    /* 判断小数后的数字是否大于0.5，如果大于0.5就显示半颗⭐ */
    const starList = this.$refs.star; // 获取所有的star标签
    const intNum = parseInt(this.docRate); // 将整数部分截取出来

    const floatNum = Number(
      "0" + this.docRate.toString().match(/(\d+)(\.\d+)/)[2]
    ); // 将小数部分截取出来

    if (floatNum >= 0.5) {
      // 需要加半个⭐的序号是前面的整数部分加1
      const halfIndex = intNum + 1;
      starList.forEach((item, index) => {
        if (index + 1 === halfIndex) {
          item.className += " half";
        }
      });
    }
  }
};
</script>

<style scoped lang="less">
.showRateStar {
  vertical-align: middle;
  height: 0.34rem;
  width: 2rem;
  position: relative;
  .rateStar {
    float: left;
    height: 0.28rem;
    margin-right: 0.2rem;
    .star {
      background: url(../assets/appointment/docdetail_icon_star_pre@2x.png)
        center no-repeat;
      background-size: 100% 100%;
      display: inline-block;
      width: 0.28rem;
      height: 0.28rem;
      margin-right: 0.08rem;
      &:nth-last-of-type(1) {
        margin-right: 0;
      }
      &.half {
        background: url(../assets/appointment/docdetail_icon_star_half@2x.png)
          center no-repeat;
        background-size: 100% 100%;
      }
      &.full {
        background: url(../assets/appointment/docdetail_icon_star@2x.png) center
          no-repeat;
        background-size: 100% 100%;
      }
    }
  }
  .score {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    float: left;
    font-size: 0.24rem;
    font-family: PingFangSC-Light, sans-serif;
    color: #272727;
  }
}
</style>


